import styled from "styled-components";
import Mediaquery from  '../mediaquerys/overflowMediaquery'

export default styled.div`
    position:absolute;
    overflow:hidden;
    height:80px;
    width: 0px;
    background-color: rgba(0, 0, 0, 0.5);
    color:white;
    bottom:45%;
    transition:all ease .3s;
    ${Mediaquery};
`